*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* background: linear-gradient(to right, rgba(7,27,82,1) 0%, rgba(0,120,120,1) 100%); */
}




.nav-container{
    display: flex;
    align-items: center;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    color: white;
    padding: 0 1.5em;
}

.nav-container>img{
    width: 30px;
    margin-right: 4px;
}

.nav-container>p{
    margin-left: auto;
    font-size: 14px;
}

@media only screen and (max-width: 400px) {
    .nav-container>p{
        font-size: 11px;
        font-weight: bold;
    }
}

.main-container{
    padding: 2.5em 2em;
}

.form{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 1em;
    gap: 1em;
}

.form>input{
    border: 1px solid #D5D4D8;
    border-radius: 5px;
    padding: 6px 0;
    text-indent: 5px;
}

.btn{
    grid-column: 1/-1;
    grid-row: 3;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    border: none;
    border-radius: 6px;
    padding: 0.5em 0;
    color: white;
    font-weight: bold;
    cursor: pointer;
}

.meme{
    margin-top: 2em;
    position: relative;
    color: white;
}

.memeimg{
    width: 100%;
    margin-top: 0;
}

.top-text{
    position: absolute;
    top: 0;
    left: 1em;
}

.bottom-text{
    position: absolute;
    bottom: 0;
    left: 1em;
}

.top-text, .bottom-text{
    -webkit-text-stroke: 1.2px black;
    font-size: 25px;
    font-family: Arial, Helvetica, sans-serif;
}
